$screen-xs: 0;
$screen-sm: 576px;
$screen-md: 768px;
$screen-lg: 992px;
$screen-xl: 1200px;
$screen-xxl: 1400px;

$gap-page: 24px;
$gap-input: 20px;

$color-dangerous: var(--color-dangerous);
$color-accent: var(--color-accent);
$color-background: var(--color-background);
$color-surface: var(--color-surface);

$color-text: var(--color-text);
$color-text-on-accent: var(--color-text-on-accent);

$color-input-border: var(--color-input-background);
$color-input-background: var(--color-input-background);

:root {
    --color-dangerous: #FF4343;
    --color-accent: #0074DF;
    --color-background: #F8F8F8;
    --color-surface: #FFFFFF;

    --color-text: #222;
    --color-text-on-accent: var(--color-surface);

    --color-input-border: #E0E0E0;
    --color-input-background: var(--color-background);
}

body {
    min-height: 100vh;
    height: 100%;
    overflow-x: hidden;
    --page-padding-x: 240px;
    color: $color-text;

    @media screen and (max-width: $screen-xxl) {
        --page-padding-x: 196px;
    }

    @media screen and (max-width: $screen-xl) {
        --page-padding-x: 128px;
    }

    @media screen and (max-width: $screen-lg) {
        --page-padding-x: 64px;
    }

    @media screen and (max-width: $screen-md) {
        --page-padding-x: 32px;
    }

    @media screen and (max-width: $screen-sm) {
        --page-padding-x: 8px;
    }
}

.lk-background {
    background: $color-background;
}

.lk-fullpage {
    min-height: 100vh;
    height: 100%;
    padding-left: var(--page-padding-x);
    padding-right: var(--page-padding-x);
    padding-top: $gap-page*2;
    padding-bottom: $gap-page*2;
}

.lk-container {
    padding: 30px;
    background-color: $color-surface;
    border-radius: 10px;
    &.lk-flex {
        display: flex;
        gap: $gap-input*2;
        flex-direction: column;
    }
    &.lk-container-mini {
        max-width: $screen-sm;
        align-self: center;
        width: 100%;
        box-sizing: border-box;
    }
    hr {
        width: 100%;
        border: 0;
        border-bottom: 1px solid $color-input-border;;
    }
    box-sizing: border-box;
}

.lk-overflow {
    overflow: auto;
}

.lk-flex-center {
    justify-content: center;
    align-items: center;
}

.lk-flex {
    .lk-flex-h {
        align-items: stretch;
        flex-direction: row;
    }
    .lk-flex-md-v {
        @media screen and (max-width: $screen-md) {
            flex-direction: column;
        }    
    }
}

.lk-flex {
    display: flex;
    flex-direction: column;
    gap: $gap-page;
}

.lk-user-info {
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 16px; 
    font-weight: 400;
    b {
        font-weight: 600; 
    }

    .lk-user-balance {
        color: $color-accent;
    }
    
    @media screen and (max-width: $screen-md) {
        flex-direction: column;
        gap:20px
    }
}

.lk-color-dangerous {
    color: $color-dangerous;
}

.lk-color-accent {
    color: $color-accent
}

.lk-input {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 4px;
    & > label {
        font-size: 14px;
        font-weight: 500;
        line-height: 150%;
    }
    > input, > textarea, > select, > button {
        box-sizing: border-box;
        font-size: 16px; 
        border-radius: 6px;
        border: 1px solid $color-input-border;
        background: $color-input-background;
        color: $color-text;
        width: 100%;
        padding: 13px;
        padding-left: 16px;
        &::placeholder {
            opacity: 0.6; 
            color: $color-text
        }
    }
    > button {
        border: none;
        background: $color-accent;
        color: $color-text-on-accent;
        text-align: center;
    }
    textarea {
        resize: vertical;
    }
    &.lk-input-check {
        > label {
            font-size: 16px;
            font-weight: 400;
        }
        flex-direction: row;
        width: auto;
        > input {
            width: auto;
        }
    }
    &.lk-search {
        flex-direction: row;
        justify-content: flex-start;
        gap: 0;
        > input {
            padding-top: 27px;
            padding-bottom: 27px;
            flex-grow: 1;
            width: 100%;
            background: $color-surface;
        }
        > button {
            width: 240px;
        }
        @media screen and (max-width: $screen-md) {
            flex-direction: column;
            gap: 8px;
            > button {
                width: 100%;
            }
        }
    }
}

.lk-input-list {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: $gap-input;
    @media screen and (min-width: $screen-lg) {
        &.lk-input-list-force-h {
            flex-direction: row;
        }
    }
}

.lk-two-row-adaptive {
    display: flex;
    flex-direction: row;
    gap: $gap-input;
    width: 100%;

    @media screen and (max-width: $screen-sm) {
        flex-direction: column;
    }
}

.lk-buttton {
    border: 0;
    font-size: 16px;
    font-weight: 600; 
    border-radius: 5px;
    background: $color-accent;
    color: $color-text-on-accent;
    padding: 20px;
    padding-left: 80px;
    padding-right: 80px;
    box-sizing: border-box;
}

.lk-table {
    box-sizing: border-box;
    font-size: 14px; 
    width: 100%;
    font-weight: 400; 
    min-width: 640px;
    overflow-x: scroll;
    th, td {
        text-align: left;
        padding-top: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid $color-input-border;
    }
    th {
        font-weight: 600;
        padding-top: 0;
    }
    tr:last-of-type > td {
        padding-bottom: 0;
        border-bottom: 0;
    }
}

a.lk-table-print {
    display: flex;
    align-items: center;
    gap: 10px;
    width: auto;
    text-decoration: underline;
    text-decoration-color: rgba($color: #000000, $alpha: 0.0);
    transition: text-decoration-color 100ms ease-in;
    border: 0;
    &:hover {
        text-decoration-color: $color-accent;
        border: 0;
    }
}

.lk-add-button {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 20px; 
    font-weight: 400;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
    &.lk-add-button-print {
        padding-top: 20px;
        padding-bottom: 20px;
        font-size: 16px; 
        font-weight: 500;
        color: $color-accent;
    }
}

.lk-no-bg {
    background: none;
    border: 1px solid $color-accent;
    &.lk-add-button {
        font-size: 16px; 
    }
}

.lk-header {
    font-size: 20px;
    font-style: normal;
    font-weight: 500; 
    margin: 0;
}

.lk-finance-info {
    display: flex;
    flex-direction: column;
    gap: 20px;
    > div {
        display: flex;
        width: 100%;
        > div {
            width: 100%;
        }
    }

    font-size: 14px;
    font-weight: 500; 

    .lk-finance-info-value {
        display: flex;
        gap: 40px;
    }
}

.lk-balance-info {
    display: flex;
    justify-content: space-around;
    width: 100%;
    font-size: 16px;
    font-weight: 400; 
    > div {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        gap: 8px;
    }
    .lk-balance-info-value {
        color: $color-accent;
        b {
            font-size: 20px;
            font-weight: 600; 
        }
    }
    @media screen and (max-width: $screen-sm) {
        flex-direction: column;
        gap: 40px;
    }
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    gap: 50px;
}

.lk-hidden {
    display: none;
}

.lk-quickfix-flex-column {
    flex-direction: column;
}

.lk-nav {
    display: flex;
    height: 118px;
    align-items: center;
    padding-left: 60px;
    padding-right: 60px;
    @media screen and (max-width: $screen-sm) {
        padding-left: 16px;
        padding-right: 16px;
        justify-content:space-between;
        gap: 0;
        height: 90px;
    }
    font-size: 13px;
    font-weight: 500;
    gap: 35px;
    .lk-nav-center {
        flex-grow: 1;
        display: flex;
        gap: inherit;
        justify-content: center;
        align-content: center;
        align-items: center;
        text-align: center;
        @media screen and (max-width: $screen-xl) {
            display: none;
        }
    }
    a {
        color: $color-text;
        text-decoration: none;
        &:hover {
            border-color: $color-accent;
        }
    }
    @media screen and (max-width: $screen-xl) {
        justify-content: space-between;
    }
}
.lk-sidebar {
    display: none;
    z-index: 1;
    @media screen and (max-width: $screen-xl) {
        position: fixed;
        top: 0;
        height: 0;
        width: 100vw;
        height: 100vh;
        flex-direction: column;
        a {
            color: $color-text;
            text-decoration: none;
            &:hover {
                border-color: $color-accent;
            }
        }
        align-items: center;
        background: $color-surface;
        font-size: 24px;
        padding: 64px;
        box-sizing: border-box;
        gap: $gap-page;
        &.lk-sidebar-visible {
            display: flex;
        }
    }
}
.lk-sidebar-open {
    display: none;
    @media screen and (max-width: $screen-xl) {
        display: block;
    }
}
.lk-clear-link {
    border: 0;
    color:inherit;
    text-decoration: none;
    &:hover {
        border: 0;
    }
}

.lk-search-checkboxes {
    display: flex;
    flex-grow: 1; 
    justify-content: space-around; 
    align-items: center;
    @media screen and (min-width: $screen-lg) {
        width: 800px;
    }
}

.lk-quickfix-header-bold {
    font-weight: 600;
}
.lk-quickfix-font-size-14 {
    font-size: 14px;
}
.inv-row {
    align-items: center;
}

.lk-dropdown {
    display: inline-block;
    position: relative;
    > .lk-dropdown-button {
        background: none;
        border: 0;
    }
    > .lk-dropdown-options {
        font-size: 14px; 
        display: flex;
        flex-direction: column;
        align-items: stretch;
        text-align: right;
        background: $color-surface;
        position: absolute;
        border-radius: 10px;
        width: 240px;
        box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.07);
        right: 0;
        > div, > a{
            padding: 20px;
            box-sizing: border-box;
        }
        a {
            border: 0;
            &:hover {
                border: 0;
            }
        }
        hr {
            width: 100%;
            margin: 0;
            border: 1px $color-input-border solid;
        }
        .lk-dropdown-links {
            display: flex;
            flex-direction: column;
            padding-top: 10px;
            padding-bottom: 10px;
            padding-left: 0;
            padding-right: 0;
            > a {
                padding-left: 20px;
                padding-right: 20px;
                padding-top: 10px;
                padding-bottom: 10px;
                &:hover {
                    background-color: #E0E0E0;
                }
            }
        }
        &.lk-hidden {
            display: none;
        }
    }
}

.lk-page-header {
    font-size: 40px;
    font-weight: 400; 
    margin: 0;
    margin-bottom: 20px;
    @media screen and (max-width: $screen-md) {
        text-align: center;
        font-size: 28px; 
    }
}

.lk-logo {
    aspect-ratio: 234 / 70;
    @media screen and (max-width: $screen-lg) {
        height: 50px;
    }
}